
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
a{
	text-decoration: none;
	color: #fff;
	font-weight: bold;
}
ul{
  margin: 0;
}

body,ul,li {
	margin: 0;
	padding: 0;
}
.cont {
	position: relative;
	height:300px;
	margin: 0 auto;
	width: 100%;
}
.cont li {
	position: absolute;
	display: inline-block;
	left: 20%;
	top: 50px;
	width: 60%;
	height:auto;
}
li.a {
	display: none;
}
li.pre {
	display: block;
	margin-left: -15%;
	z-index: 50;
	-webkit-transition: all .8s;
	transition: all .8s;
	cursor: pointer;
	opacity: .75;
}
li.next {
	display: block;
	margin-left: 15%;
	z-index: 50;
	-webkit-transition: all .8s;
	transition: all .8s;
	cursor: pointer;
	opacity: .75;
}
li.now {
	display: block;
	z-index: 88;
	-webkit-transform: scale(1.3);
	        transform: scale(1.3);
	-webkit-transition: all .8s;
	transition: all .8s;
}
.cont li img {
	width: 100%;
	height:auto;
	-webkit-box-shadow: 0 2px 20px #aaa;
	        box-shadow: 0 2px 20px #aaa;
}

*{
	padding: 0;
	margin: 0;
}
li{
	list-style-type: none;
}
.carousels_box{
}
.carousels_box ul{
	position: relative;
	width: 100%;
	height: 500px;
	margin: 0 auto;
}
.carousels_box li{
	position: absolute;
	-webkit-transition: all .8s;
	transition: all .8s;
	width: 60%;
}
.carousels_box img{
	width: 100%;
}
.first{
	left: 0;
	z-index: 1;
	-webkit-transform: scale(.85);
	        transform: scale(.85);
	opacity: .5;
}
.two{
	left: 10%;
	z-index: 2;
	-webkit-transform: scale(.95);
	        transform: scale(.95);
	opacity: .75;
}
.three{
	left: 20%;
	z-index: 3;
}
.four{
	left:30%;
	z-index: 2;
	-webkit-transform: scale(.95);
	        transform: scale(.95);
	opacity: .75;
}
.five{
	left: 40%;
	z-index: 1;
	-webkit-transform: scale(.85);
	        transform: scale(.85);
	opacity: .5;
}

.container{
	width:100%;
	height:500px;
}
.view_box{
	width: 80%;
	height: 450px;
	margin: 0 auto;
	-webkit-transition: all .8s;
	transition: all .8s;
}
.btn div{
	display: inline-block;
	width:120px;
	height: 65px;
	margin: 20px 5px;
	border-radius: 5px;
	background-color: #5ccccc;
}
.btn div p{
	margin-top: 20px;
}

li{
	list-style: none;
}
.cont-box{
	position: relative;
	margin: 0 auto;
	height: 100%;
	width: 80%;
}
.img-box{	
	width: 100%;
}
.img-box img{
	width: 100%;
	height: auto;
}
.img-box>ul{
	position: absolute;
	width: 100%;
	height: 100%;
	margin: 0 auto;
	padding: 0;
	overflow: hidden;
}
.img-box>ul>li{
	width: 100%;
	height: auto;
	position: absolute;
}
.zibiao{
	position: absolute;
	height:100%;
	width: 100%;
	top: 0;
	left: 0;
}
.zibiao>div{
	cursor: pointer;
	clear: both;
	top: 50%;
	width: 20px;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	height: 50px;
	background-color: gainsboro;
	line-height: 50px;
	position: absolute;
}
.you{
	right: 0;
}
.list-box ul{
	position: absolute;
	width: 100%;
	bottom: -30px;
	margin: 0 auto;
	z-index: 10;
	text-align: center;
}
.active{
	background-color:#000000;
}
.list-box li{
	cursor: pointer;
	width: 10px;
	text-align: center;
	height: 10px;
	border-radius: 10px;
	border: 1px solid black;
	display: inline-block;
	margin: 5px;
}
.image-enter-active {
	/*opacity: 1;	*/				   /*淡入淡出*/
    
    -webkit-transform: translateX(0);
    
            transform: translateX(0);		/*划入划出*/
    -webkit-transition: all 1.5s ease;
    transition: all 1.5s ease;
}
.image-leave-active {
  	/*opacity: 0;*/
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: all 1.5s ease;
    transition: all 1.5s ease;
}
.image-enter {
  	/*opacity: 0;*/
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
.image-leave {
  	/*opacity: 1;*/
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
